html {
  background-color: black; }

.App {
  position: relative;
  margin: 0;
  padding: 0;
  width: 100vh;
  height: 100vh;
  display: grid;
  grid-template-columns: 20% 80%;
  justify-content: center; }

nav {
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  background: #970000;
  border: 5px solid rgba(157, 157, 157, 0.4);
  display: grid;
  grid-template-rows: 20% 80%;
  grid-template-areas: "User" "Links"; }

.NavBar-Signout {
