    .NavBar-Signout i:hover {
      color: #3a3a3a;
      text-shadow: white 1px 1px 3px; }
  .NavBar-Signout h4 {
    display: inline;
    margin: 5px;
    color: white; }
    .NavBar-Signout h4:hover {
      color: #3a3a3a;
      text-shadow: #500000 .5px .5px 1px; }

.signin-icon {
  font-size: 24px;
  color: silver;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.5); }

.NavButton--container {
