    border: none;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.55); }
  .NavButton--container .active {
    background: #500000;
    border: 4px solid rgba(0, 0, 0, 0.25);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: 0px 20px 20px 0px; }
  .NavButton--container a {
    text-align: center;
    line-height: 50px;
    height: 50px;
    text-decoration: white none;
    color: white;
    background: none;
    border: none;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.55); }

.CardContainer {
  position: relative;
  box-sizing: border-box;
  width: 90vw;
  height: 100vh;
  display: grid;
  grid-template-rows: 10% 90%;
  background-color: #3a3a3a;
  background: url("https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80");
  background-size: cover;
  backdrop-filter: 80%; }

.display-divider {
  content: '';
  top: 9.5%;
  left: 1%;
  position: absolute;
  width: 95%;
  height: 5px;
  z-index: 4;
  background-color: #500000;
  border-radius: 40%;
  border-bottom: 3px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 5px black; }

header {
  background-color: black;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 20px 5%; }
  header .title {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    left: 15px; }
    header .title h1 {
      color: #970000;
      font-size: 34px; }
  header img {
    width: 30px;
    height: 30px;
    margin-right: 5px; }

.cards-display {
  display: flex;
  flex-wrap: wrap;
  padding: 0 5%;
  overflow: scroll; }

.SmallCard {
  display: grid;
  grid-template-rows: 60% 40%;
  align-items: center;
  position: relative;
  background: rgba(0, 0, 0, 0.51);
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.585);
  width: 250px;
  height: 356px; }
  .SmallCard .image--container {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    background-color: silver;
    z-index: 1; }
    .SmallCard .image--container img {
      object-fit: cover;
      width: 100%;
      height: 100%; }
  .SmallCard .content {
    color: white;
    background: linear-gradient(rgba(0, 0, 0, 0.8), #640000);
    border-radius: 20% 20% 0 0;
    padding: 15px;
    z-index: 3;
    position: absolute;
    bottom: 0px;
    height: 40%; }
    .SmallCard .content h3 {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 50px;
      margin: 0;
      text-align: center;
      text-shadow: 2px 2px 3px black;
      overflow: scroll; }
    .SmallCard .content p {
      margin-bottom: 0;
      text-shadow: 1px 1px 3px black; }
    .SmallCard .content .sub-content {
      position: relative;
      display: flex;
      align-items: flex-end;
      flex-direction: column; }
      .SmallCard .content .sub-content .sub-border {
        content: '';
        width: 100%;
        height: 3px;
        box-shadow: 1px 1px 3px black;
        background-color: #500000;
        border-radius: 40%; }
      .SmallCard .content .sub-content .showmore {
        text-align: right; }
  .SmallCard .rating {
    position: absolute;
    bottom: 10px;
    left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background: #970000;
    box-shadow: 0px 4px 4px #000000;
    width: 35px;
    height: 35px;
    border-radius: 50%;
    color: white; }
  .SmallCard .faveBtn {
    position: absolute;
    z-index: 5;
    color: silver;
    font-size: 25px;
    bottom: 10px;
    left: 50px;
    background: none;
    border: none; }
    .SmallCard .faveBtn:hover {
      color: #e8b51b; }

.CardContainer a {
  margin: 20px 20px; }

.CardContainer .rating {
  z-index: 5; }

.fave-true {
  color: #e8b51b; }
  .fave-true:hover {
    color: #5b5b5b; }

main {
  position: relative;
  box-sizing: border-box;
  width: 90vw;
  height: 100vh;
  display: grid;
  grid-template-rows: 10% 90%;
  background-color: #3a3a3a;
  background: url("https://images.unsplash.com/photo-1489599849927-2ee91cede3ba?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80");
  background-size: cover;
