.BigCard {
  background-color: #500000;
  border-radius: 30px;
  position: absolute;
  top: 25%;
  left: 15%;
  width: 60vw;
  height: 50vh;
  z-index: 15;
  color: silver;
  box-shadow: 5px 5px 10px black; }
  .BigCard h2 {
    font-size: 28px;
    margin: 0; }
  .BigCard .backdrop {
    position: absolute;
    width: 50%;
    height: 100%;
    z-index: 2;
    border-right: 12px outset #690e0eb7;
    border-radius: 0px 120px 120px 0px; }
  .BigCard .big-content {
    position: absolute;
    z-index: 10;
    width: 40%;
    right: 5%;
    top: 15%; }
  .BigCard .big-subcontent {
    border-top: 3px solid silver;
    border-bottom: 3px solid silver;
