.existingUser,
.newUser {
  display: grid;
  grid-template-rows: 1fr 3fr 1fr;
  grid-template-columns: 100%;
  justify-content: center;
  align-items: center;
  background-color: #970000;
  height: 50vh;
  width: 25vw;
  padding: 20px;
  box-shadow: black 5px 5px 10px; }
  .existingUser input,
  .newUser input {
    background: #3a3a3a;
